export function usePagination(){

    const page_sizes = [10,20,30,40,50]
    let page = 1
    let totalCount =0
    let page_size = 2

    const setTotalCount=(count:number)=>{
        totalCount = count
    }

    const setPage=(page:number)=>{
        page = page
    }
    
    const setPageSize=(size:number)=>{
        page_size = size
    }
    

    const handleNextPage=()=>{

      if(page<totalCount/10){
          setPage(page+1)
        
      }
     
    }

    const handlePrevPage=()=>{
      if(page>1){
          setPage(page-1)
      
      }
      
    }

    return{
        page,
        totalCount,
        setPage,
        setTotalCount,
        handleNextPage,
        handlePrevPage,
        page_sizes,
        page_size,
        setPageSize
    }

}