'use client'

import AddCategory from "@/components/category/AddCategory"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Separator } from "@/components/ui/separator"
import { CategoryModel } from "@/type/category.model"
import { useEffect, useState } from "react"
import { categoryService } from "@/lib/services/categoryService"
import { usePagination } from "@/hook/usePagination"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import {format} from "date-fns"

export default function Page(){

    const [search, setSearch] = useState<string>("")
    const [open, setOpen] = useState<boolean>(false)
    const [categoriesData, setCategoriesData] = useState<CategoryModel[]>([])
    const [actionType, setActionType] = useState<string>('add')
    const [categoryDetail, setCategoryDetail] = useState<CategoryModel>()
    const [isLoading, setIsLoading] = useState<boolean>(false)
   
    const [totalPage, setTotalPage] = useState<number>(0)
    const pagination = usePagination()
    const getCategories =()=>{
       setIsLoading(true)
       categoryService.getCategories({page:pagination.page,page_size:pagination.page_size,search}).then((res)=>{
          setIsLoading(false)
          setCategoriesData(res.data.data.results)
          setTotalPage(res.data.data.count)
       }).catch((err)=>{
        setIsLoading(false)
        console.log(err)
       })

    }

    useEffect(()=>{
     getCategories()
    },[])


        const handleUpdateButton=(catagoryDetail:CategoryModel)=>{
              setCategoryDetail(catagoryDetail)
            
              setActionType("edit")
                setOpen(true)
        }
    
        const handleDeleteBook = async(id:string)=>{
          setIsLoading(true)
          categoryService.deleteCategory(id).then((res)=>{
            setIsLoading(false)
            setCategoriesData((prev)=>{
              return prev.filter((category:any)=>category?.id !== id)
            })
          }).catch(error=>{
            setIsLoading(false)
            console.log(error)
          })
        }
    

    useEffect(()=>{
       if(!open){
            setActionType('add')
           setCategoryDetail({
             id:'',
             name:'',
             description:'',
             created_date:'',
             updated_date:''
           })
       }
    },[open])

    return(
         <div className="w-full">
            <div className="flex justify-between items-center px-12 py-4">
              <input className="max-w-md bg-white px-4 py-3 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500" 
              placeholder="search"
              onChange={(e)=>setSearch(e.target.value)}
              />
              
              <Dialog open={open} onOpenChange={setOpen}>
  <DialogTrigger className="bg-black hover:bg-gray-500 cursor-pointer text-white px-4 py-2 rounded-lg shadow-lg" >Add Category</DialogTrigger>
  <DialogContent>
    <DialogHeader>
      <DialogTitle>Add Category</DialogTitle>
    </DialogHeader>
    <Separator />
     
     <AddCategory  setOpen={setOpen} actionType={actionType} categoryDetail={categoryDetail} getCategories={getCategories}/>
    
  </DialogContent>
</Dialog>
            </div>
            <div className="flex flex-col justify-center">
                 <Table className="bg-white">
                     <TableHeader>
                       <TableRow>
                            <TableHead>Category Id</TableHead>
                            <TableHead>Category Name</TableHead>
                            <TableHead>Description</TableHead>
                            <TableHead>Created Date</TableHead>
                            <TableHead>Action</TableHead>
                       </TableRow>
                     </TableHeader>
                     <TableBody>
                       {
                        !isLoading && categoriesData?.map((category,index)=>{
                          return(
                            <TableRow key={index}>
                                <TableCell>{category.id}</TableCell>
                                <TableCell>{category.name}</TableCell>
                                <TableCell>{category.description}</TableCell>
                                <TableCell>{format(category?.created_date,'dd-mm-yyyy')}</TableCell>
                                <TableCell>
                                       <DropdownMenu>
          <DropdownMenuTrigger>
            <MoreHorizontal className="h-5 w-5 text-gray-400"/>
            </DropdownMenuTrigger>
          <DropdownMenuContent className="w-18">
             
             <DropdownMenuLabel className="flex items-center text-gray-700 cursor-pointer">
              <Popover >
                <PopoverTrigger className="flex items-center text-gray-700 cursor-pointer">
                   <DeleteIcon className="mr-4 text-[12px]"/>
              Delete
                </PopoverTrigger>
                <PopoverContent className="w-64">
                  are you sure to delete this book?
                  <div className="flex justify-center mt-4">
                      <Button className="text-red-600 bg-transparent outline-red-600">No</Button>
                      <Button className="text-green-600 bg-transparent outline-green-600" onClick={()=>handleDeleteBook(book.id)}>Yes</Button>
                  </div>
                  
                </PopoverContent>
              </Popover>
             </DropdownMenuLabel>
             <DropdownMenuLabel className="flex item-center text-gray-700 cursor-pointer" onClick={()=>handleUpdateButton(book)}>
              <Edit className="mr-4 text-[12px]"/>
              Update</DropdownMenuLabel>
          </DropdownMenuContent>
        </DropdownMenu>
                                </TableCell>      
                            </TableRow>
                          )
                        })
                          
                       }
                     </TableBody>
                 </Table>
            </div>
         </div>
    )
}